@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
body {
  margin: 0;
  padding: 0;
  background-color: #F5F5F5;
  font-family: 'Open Sans', sans-serif; }

.App-header {
  background-color: #3DAFD3;
  width: 100vw;
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  z-index: 1; }

.Header__content {
  width: 100vw;
  max-width: 1100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }

.Hand-tapping {
  color: #FFFFFF;
  margin-left: 15px;
  margin-right: 26px;
  -webkit-transform: rotate(200deg);
          transform: rotate(200deg); }
  @media all and (min-width: 768px) {
    .Hand-tapping {
      margin-right: 86px; } }

.Menu {
  width: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; }

a {
  text-decoration: none; }

.Show-songs__link, .Show-albums__link {
  color: #696969;
  margin-right: 25px; }

.Show-songs__link.is-active, .Show-albums__link.is-active {
  color: #FFFFFF; }

.Favourites-counter__container {
  color: #FFFFFF;
  margin-left: auto;
  position: relative; }

.Header__heart {
  padding-right: 24px; }
  @media all and (min-width: 768px) {
    .Header__heart {
      padding-right: 44px; } }

.Favourites__counter {
  position: absolute;
  right: 18px;
  top: -8px;
  background-color: #A0A0A0;
  color: #FFFFFF;
  border-radius: 30px;
  padding-top: 2px;
  font-size: 10px;
  width: 15px;
  height: 14px;
  text-align: center; }
  @media all and (min-width: 768px) {
    .Favourites__counter {
      right: 38px; } }

.Search-field__input {
  height: 26px;
  width: 186px;
  background-color: #3da6c6;
  border: none;
  border-radius: -6px;
  outline: none;
  color: #FFFFFF;
  padding-left: 8px; }

.Search-field__input::-webkit-input-placeholder {
  color: #FFFFFF;
  padding-left: 8px; }

.Search-field__input::-ms-input-placeholder {
  color: #FFFFFF;
  padding-left: 8px; }

.Search-field__input::placeholder {
  color: #FFFFFF;
  padding-left: 8px; }

.Magnify {
  color: #FFFFFF;
  position: absolute;
  margin: 2px 0 0 -29px;
  font-size: 20px; }

.Main-section {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh; }

.Songs__list {
  width: 1100px;
  height: 100vh;
  margin: 130px 0 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  list-style: none; }

.Song__item {
  position: relative;
  width: 250px;
  height: 320px;
  background-color: #FFFFFF;
  margin: 10px;
  list-style: none;
  border: solid 1px #D3D3D3;
  border-radius: 0 0 5px 5px; }
  @media all and (min-width: 768px) {
    .Song__item {
      width: 230px;
      height: 360px; } }

.Song__sleeve {
  width: 250px;
  height: 132px; }
  @media all and (min-width: 768px) {
    .Song__sleeve {
      width: 230px;
      height: 160px; } }

.Card__info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 220px; }

.Favourites__heart {
  border: none;
  outline: none;
  background: none;
  color: #3DAFD3;
  position: absolute;
  bottom: 10px;
  right: 10px; }

.Song__title {
  padding-left: 10px;
  font-size: 14px; }
  @media all and (min-width: 768px) {
    .Song__title {
      font-size: 16px; } }

.Song__album {
  padding-left: 10px;
  font-size: 12px; }
  @media all and (min-width: 768px) {
    .Song__album {
      font-size: 13px; } }

